.translate {
    display: flex;
    flex-direction: row;

    @media (min-width: 300px) and (max-width: 700px) {
        flex-direction: column;
    }
    
    &__layout {
        @include layout;
        position: relative;

        &__container {
            width: 90%;
            
            @media (min-width: 300px) and (max-width: 700px) {
                width: 100%;
            }
        }
    }

    &__alert {
        position: absolute;
        display: flex;
        justify-content: center;
        background-color: $extra_light_primary;
        color: $dark_primary;
        padding: 7px 15px;
        border-radius: 10px;
        margin-top: 40px;
    }

    &__box {
        margin-top: 50px;
        margin-bottom: 10px;
        display: flex;
        flex-direction: column;
        padding: 0 10px;
        width: 100%;
        box-sizing: border-box;

        @media (min-width: 300px) and (max-width: 700px) {
            margin-bottom: 0;
        }

        & > textarea {
            height: 100%;
            @include input;
            resize: vertical;
            height: 80px;
        }

        &__textarea{
            direction: rtl !important;
        }
        
        &__title {
            margin: 7px 0;
            font-size: 18px;
            font-weight: 500;
            color: $dark_secondary;
        }

        // &__translateBox {
        //     background-color: $secondary;
        //     padding: 15px;
        //     border-radius: 10px;
        //     height: 100%;
        //     height: 80px;
        //     box-sizing: border-box;
        // }

        &__lang {
            margin: 0 10px;
            font-size: 10px;
            font-weight: 200;
            color: $dark_secondary;
        }

        &__translateTitle {
            display: flex;
            align-items: center;
        }

        &__wordTitle {
            display: flex;
            flex-direction: row-reverse;
            align-items: center;

            @media (min-width: 300px) and (max-width: 700px) {
                flex-direction: row;
            }
        }
    }

    &__addBtn {
        background-color: $extra_dark_secondary;
        position: fixed;
        bottom: 20px;
        right: 20px;
        
        & > span, svg {
            color: $dark_primary;
        }
    }

    &__btn {
        padding: 60px 0;
        display: flex;
        justify-content: center;

        &__translate {
            background-color: $extra_light_primary;

            & > svg, span {
                color: $primary;
            }
        }
    }
}

